*{
    margin:0;
    padding:0;
    text-decoration: none;
    font-family: "Roboto Mono", monospace;
    font-optical-sizing: auto;
   
}



body {
    background-color: #000;
    
}

.container{
    display:flex;
    
}
main{
    width: 100%;
    padding: 20px;
}
.sidebar{
    gap: 1em;
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0deg, rgba(12, 12, 12, 0.678) 0%, rgba(20, 20, 20, 0.664) 100%);
    max-width: 1300px;
    align-self: center;
    margin-left: auto;
    margin-right: auto;
    top: 0;
    left: 0;
    right: 0;
    
    color: #fff;
    z-index: 100;
    height: 8em;
    border-bottom-right-radius: 1em;
    transition: all 0.5s;
    backdrop-filter: blur(4px);
    border-bottom-left-radius: 1em ;
    border-bottom-right-radius: 1em ;
}
.top_section{
    display: flex;
    align-items:center;
    padding:20px 15px;
}
.logo{
    font-size: 30px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}
.link{
  
    display: flex;
    color: #fff;
    padding: 10px ;
   font-weight: 100;
    transition: all 0.5s;
    border-radius: 0.5em;
}
.link:hover{
    background:white;
    color: #000;
    transition: all 0.5s;
    font-weight: 900;
}
.active{
    background: orange;;
    color: #000;
    font-weight: 900;
}
.icon, .link_text{
    font-size: 1em;
    margin-left: 0.5rem;
}
.icon2{
  font-size: 1em;
  margin-right: 0.5rem;
}


.TopbarMaster {
    position: fixed;
    top: 0%;
    bottom: 100%;
    height: 4em;
    width: 100%;
    margin-bottom: 2em;
    border-bottom: 1px solid white;
}

.topbarMain {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    color: white;
   margin-left: 1em;
}

.idealLogo {
    width: auto;
    height: 4em;

}

.menuItem {
    width: 15%;
    height: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
    cursor: pointer;
    transition: all 0.5s;
}

.menuItem:hover {
    background-color: orange;
    color: #000;
    font-size: 1.1em;
    font-weight: 900;
    height: 80%;
}


.idealLogo { 
    width: 80%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.Logo1 {
    height: 95%;
    margin-left: 2em;
    transition: all 0.5s;
}

.Logo2 {
    height: 100%;
    width:10em;
    transition: all 0.5s;
    margin-right: 2em;
}

.socialsIcons {
    width: 50%;
    height: 70%;
}


.pagesContainers {
    max-width: 1300px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    align-self: center;
    margin-top: 8em;
    color: #fff;
    text-shadow: 4px 3px 4px rgba(0,0,0,0.89);
}

.mainHome { 
    width: 100%;

    margin-left: auto;
    margin-right: auto;

    
}

.fondoContainer {
    z-index: 0;
    height: 60vh;
    width: 100vw;
    overflow: hidden;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 0;
    justify-content: center;
    justify-self: center;
    justify-items: center;
    align-items: center;
    align-self: center;
    align-content: center;
  background-image: url(./assets/fondo2.webp);
  background-position: left;
  background-size: cover;
  
}

.FondoCarnes {
    width: 100%;
    margin-top: -14%;
    height: auto;
    opacity: 0.6;
    z-index: 0;
    position: relative;
}

.homeContent {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 100;
    justify-content: center;
    align-items: center;
}

.homeTitulo {
   z-index: 10;
   font-size: 2.5em;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    color: orange;
    align-items: center;
    align-self: center;
    align-content: center;
    padding: 1em;
}

.VerProductosBoton {
    width: 200px;
    background-color:orange;
    border: none;
    height: 3em;
    border-radius: 0.8em;
    font-weight: 900;
    transition: all 200ms ease-in-out;
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-self: center;
    align-items: center;
    font-size: 1em;
}

.VerProductosBoton:hover {
    background-color: white;
    color: #000;
    transform: scale(1.2);
    
}

.homeButtons {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
   display: grid;
   flex-direction: row;
    align-items: center;
    align-self: center;
    align-content: center;
    justify-content: center;
    gap: 2em;
    
}



.swiper {
    width: 100%;
    height: 100%;
    margin-top: 10% ;
    
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #0000009a;
    font-weight: 900;
    
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-pagination {
    display: none;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-height: 150px;
  }

  .ProductsMain {
    display: flex;
    flex-direction: column;
    width: 100%;
    
    height: auto;
  }

  .ProductosHead {
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.11);
    height: 5em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
  }

  .ProductosMenuyLista {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    text-shadow: none;
 
  }

  .ProductosMenu { 
    width: 20%;
    border: 1px solid rgba(255, 255, 255, 0.11);
    height: 100%;
    padding: 0.5em;
    background-color: #0f0f0f;
    display: flex;
    flex-direction: column;
  }
  
  .ProductosLista {
    width: 100%;
    border: 1px solid rgba(255, 255, 255, 0.11);
    height: 100%;
    padding: 0.5em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
    gap: 0.5rem;
  }

  .stacked {
    display: grid;
  }

  .stacked > * {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  .card {
    
    padding-left: 0.5em;
    aspect-ratio: 1 / 1.4;
    margin-bottom: 2rem;
    border: 1px solid #ffffff18;
  }

  .card_content {
    align-self: end;
  }

  .img_card {
    width: 100%;
    max-width: 100%;
    aspect-ratio: 1 / 1;
    object-fit: cover;
  }

  .card_titulo {
    color: aqua;
    
  }

  .card_descripcion {
    font-weight: 900;
  }

  .card_precio {
    color: orange;
  }


@media screen and (max-width: 700px) and (orientation: portrait){
  .pagesContainers {
    margin-top: 3rem;
  }

  .sidebar{
    gap: 0em;
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    max-width:100%;
    margin-left: 0;
    
    left: 0;
  
    height: 80px;
  
}
.Logo2 {
  width: 60px;
}
.link_text2 {
  font-size: 80%;
}
.link {
  padding:5px ;
}
.top_section {
  padding-right: 0;
  width: 65px;
}


    .ProductosMenuyLista {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    }
    .ProductosMenu {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) ;
      width: 90%;
      align-items: center;
    }
    .idealLogo {
        width: 100%;
    }
    .menuItem {
        display: none;
    }

    .fondoContainer {
        height: 60vh;
    }
    .FondoCarnes {
    margin-top: 0%;
    height: 100%;
    }
    .swiper {
        
        margin-top: 60%;
        
      }
      .swiper-slide img {
        
        max-height: 500px;
      }
   
}

@media screen and (max-width: 1440px) and (orientation: landscape) {
    .swiper {
        
        margin-top: 8% !important;
        
      }
    
}

@media screen and (max-width: 1920px) and (orientation: landscape) {
    .swiper {
        
        margin-top: 15%;
        
      }
    
}